@import url(https://fonts.googleapis.com/css?family=Spartan);
@import url(https://fonts.googleapis.com/css?family=Cabin);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* OVERALL */

body {
	background-color: #F8F8F7;
	color: #464646;
	font-family: 'Spartan', sans-serif;
	padding: 5%;
	text-align: center;
	height: 100vh;
	min-width: 750px;
}

a, a:hover {
	text-decoration: none;
	color: #464646;
}

p {
	font-size: 12pt;
	margin: 5px;
}

h1 {
	font-size: 28pt;
	font-weight: 800;
	font-family: 'Questrial', sans-serif;
	opacity: 0.9;
}

/* NAVBAR */

nav {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	margin-bottom: 2.5%;
}

.icon {
	width: 30px;
	height: 30px;
	opacity: 75%;
}

.icon:hover {
	opacity: 30%;
}

.right {
	margin-left: 30px;
}

.left {
	margin-right: 30px;
}

.center {
	text-align: center !important;
	padding-bottom: 2.5% !important;
}

.fade-in {
	-webkit-animation-name: fade-in;
	        animation-name: fade-in;
	-webkit-animation-duration: 2s;
	        animation-duration: 2s;
}

@-webkit-keyframes fade-in {
	0% {
		opacity: 0;
	} 100% {
		opacity: 1;
	}
}

@keyframes fade-in {
	0% {
		opacity: 0;
	} 100% {
		opacity: 1;
	}
}

.home {
	height: 70vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.about {
	margin-top: 5%;
	display: flex;
	flex-direction: row;
}

.about p {
	padding: 5px 50px 20px 50px;
	text-align: left;
	line-height: 1.4;
	margin: 0;
	min-width: 400px;
}

.about h1 {
	text-align: center;
	white-space: nowrap;
}

.picture {
	position: relative;
	height: 225px;
	width: 225px;
	margin: 0 5% 2.5% 5%;
}

.picture img {
	display: inline;
	height: 200px;
	margin: 0;
	position: absolute;
	top: 12.5px;
	left: 12.5px;
}

.offset {
	height: 200px;
	width: 200px;
	border-style: solid;
	border-width: 2px;
	position: absolute;
	border-color: lightgray;
	z-index: -1;
}

.down {
	margin-top: 25px;
	margin-right: 25px;
}

.up {
	margin-bottom: 25px;
	margin-left: 25px;
}

@media (max-width: 600px) {
	.picture {
		display: none;
	}
	.about p {
		min-width: 0px;
	}
	
}

.video-container {
    position: relative;
    padding: 0;
}

.video-player {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0;
}

.video-player:hover {
    cursor: pointer;
}

.strike {
	position: relative;
	line-height: 30px;
}

.strike:hover {
	cursor: pointer;
	-webkit-animation: strikethrough;
	        animation: strikethrough;
	opacity: 0.7;
}

@-webkit-keyframes strikethrough {
	0% {
		-webkit-transform: scaleX(0);
		        transform: scaleX(0);
	}
	100% {
		-webkit-transform: scaleX(1);
		        transform: scaleX(1);
	}
}

@keyframes strikethrough {
	0% {
		-webkit-transform: scaleX(0);
		        transform: scaleX(0);
	}
	100% {
		-webkit-transform: scaleX(1);
		        transform: scaleX(1);
	}
}

.strike:hover:after {
  content: ' ';
  position: absolute;
  display: block;
  top: 50%;
  left: 0;
  width: 100%;
  height: 2px;
  background: #464646;
  -webkit-transform-origin: 0% 50%;
          transform-origin: 0% 50%;
  -webkit-animation: strikethrough .3s ease-out;
          animation: strikethrough .3s ease-out;
}

.projects {
	position: relative;
    display: grid;
    grid-template-columns: repeat(auto-fit, 240px);
    grid-template-rows: repeat(auto-fit, 325px);
    grid-auto-rows: 325px;
    justify-content: center;
    padding-bottom: 5%;
}

.thumbnail {
    border-radius: 5px;
}

.thumbnail:hover {
    opacity: 0.75;
    transition: 0.2s;
    cursor: pointer;
    border-radius: 15px;
}

.card-container {
    margin: 0px 5px 0px 5px;
    
}

.card {
    margin-bottom: 10px;
    border-radius: 0;
    border: none;
    background-color: #F8F8F7;
}

.card-body {
    padding: 20px;
}

.title {
    font-weight: 800;
    font-size: 12pt;
    text-align: left;
    font-family: 'Questrial';
    opacity: 0.9;
}

.subtext {
    font-size: 9.5pt;
    opacity: 0.85;
    text-align: left;
}

.overlay {
    z-index: 1;
    position: absolute;
    background-color: whitesmoke;
    width: 100%;
    height: 100%;
}

.header {
	position: relative;
	text-align: left;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 2.5%;
}

.line {
	width: 100%;
	margin-left: 20px;
    color: black;
}

